import React, { createContext } from 'react';
import './App.css';
import { createBrowserRouter, RouterProvider, useParams } from 'react-router-dom';
import Home from './component/Home';
import Navbar from './component/Navbar';
import SupportChatDocumentation from './component/SupportChatDocumentation';
import ChatAdmin from './component/ChatAdmin';
import ProtectedRoute from './component/ProtectedRoute';
import AddChatPage from './component/AddChatPage';
import Unautherized from './component/Unautherized.js';

function App() {
  const { id } = useParams();
  const data = createContext();
  const router = createBrowserRouter([
    {
      path: "/",
      // element: <data.Provider value={"test"}><Navbar /><Home /><Chat /></data.Provider>
      element: <data.Provider value={"test"}><Navbar /><Home /></data.Provider>
    },
    {
      path: "/about",
      element: <data.Provider value={"test"}><Navbar /><div>This is About Section Page I will show hear Projects and Skills and project running area like a another window in this tab and this another windows will show the running project.</div></data.Provider>
    }, {
      path: '/chatadmin',
      element: <data.Provider value={"test"}><Navbar /><ChatAdmin /></data.Provider>
    }, {
      path: "/chatadminhome",
      // element: <data.Provider value={"test"}><Navbar /><AddChatPage /></data.Provider>
      element: <ProtectedRoute element={<data.Provider value={"This is  Protected route"}><Navbar /><AddChatPage /></data.Provider>} />
    },
    {
      path: "/supportchatdocumentation",
      // element: <data.Provider value={"test"}><Navbar /><AddChatPage /></data.Provider>
      element: <SupportChatDocumentation />
    },
    {
      path: "/unautherized",
      // element: <data.Provider value={"test"}><Navbar /><AddChatPage /></data.Provider>
      element: <Unautherized />
    }
  ])
  return (
    <RouterProvider router={router} />
  )
}
export default App;
